@import url('https://fonts.googleapis.com/css?family=Fredoka+One|Oleo+Script+Swash+Caps|Spartan&display=swap');
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#title {
  font-family: 'Oleo Script Swash Caps', cursive;
  letter-spacing: .8px;
  opacity: .7;
  font-size: 3em;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#board {
  /* margin: 0; */
  display: flex;
  justify-content: center;
}

.column {
  padding-right: 20px;
}